@import "tailwindcss/base";
@import "tailwindcss/components";
@import "tailwindcss/utilities";

@font-face {
  font-family: "AngerpoiseLampshade";
  src: url("./fonts/AngerpoiseLampshade.otf");
}

@font-face {
  font-family: "RelishGargler";
  src: url("./fonts/RelishGargler.otf");
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  scroll-behavior: smooth;
  background-color: #1c1917;
}

/* HTML: <div class="loader"></div> */
.loading-wave {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center; /* Centers the bars vertically in the container */
}

.loading-bar {
  width: 100%;
  height: 50%; /* Initial height set to 50% */
  margin: 0 5px;
  animation: loading-wave-animation 1s ease-in-out infinite;
  transform-origin: center; /* Sets the middle point as the anchor */
}

@keyframes loading-wave-animation {
  0% {
    height: 50%; /* Initial height */
  }
  50% {
    height: 100%; /* Full height */
  }
  100% {
    height: 50%; /* Back to initial height */
  }
}